import styled from "@emotion/styled";
import { DeviceUUID } from "device-uuid";
import { ClearButton } from "@/atoms";

import { ReactComponent as HeartA } from "./svg/heartA.svg";
import { ReactComponent as HeartB } from "./svg/heartB.svg";
import { useSetRecoilState } from "recoil";
import { likeStatus } from "@/recoil";
import React from "react";

export const LikeButton: React.FC<{
  image: string;
  count: number;
  isLiked: boolean;
}> = React.memo(({ image, count, isLiked }) => {
  const setLikes = useSetRecoilState(likeStatus);

  const onClick = async () => {
    const id = image.replace(/(\/|\.(png|jpg|jpeg))/g, "");

    await fetch(`https://staysharp0.synology.me:8090/likes/${id}`, {
      method: isLiked ? "DELETE" : "POST",
      headers: {
        "Content-Type": "application/json",
        UUID: new DeviceUUID().get(),
      },
    });

    setLikes((likes) => ({
      ...likes,
      [id]: { count: isLiked ? count - 1 : count + 1, isLiked: !isLiked },
    }));
  };

  return (
    <Button onClick={onClick}>
      {isLiked ? <HeartActive /> : <Heart />}  {count}
    </Button>
  );
});

const Heart = styled(HeartA)`
  position: relative;
  top: 3px;
  margin-right: 5px;
  width: 23px;
  height: auto;
`;

const HeartActive = styled(HeartB)`
  position: relative;
  top: 3px;
  margin-right: 5px;
  width: 23px;
  height: auto;
`;

const Button = styled(ClearButton)`
  position: absolute;
  left: 0;

  font-family: "SeoulHangangM";
  font-size: 1.1rem;
  line-height: 22px;
`;
