import { CopyToClipboard } from "react-copy-to-clipboard";

import { ClearButton, Component } from "@/atoms";

import { ReactComponent as COPY } from "./svg/info-copy.svg";

export const AccountButton: React.FC<{ text: string }> = ({ text }) => {
  return (
    <CopyToClipboard
      text={text}
      onCopy={() => alert(" .")}
    >
      <ClearButton
        style={{
          position: "absolute",
          top: "20%",
          right: "-5%",
        }}
      >
        <Component Base={COPY} width={230} />
      </ClearButton>
    </CopyToClipboard>
  );
};
